<template>
	<view class="index">
		<!--  轮播图 -->
		<view class="swiper"><u-swiper :effect3d="true" :list="swiperData" name="img_url" height="320"></u-swiper></view>
		<!-- tabs切换栏 -->
		<view class="tabs"><u-tabs name="cate_name" active-color="#d4237a" :list="categoriesData" :is-scroll="false" :current="current" @change="change"></u-tabs></view>
		<!-- 商品列表 -->
		<view class="goods">
			<view class="goodsList" v-for="item in this.goodsData.data" :key="item.id">
				<view class="goodsList_img"><u-image width="100%" height="300rpx" :src="item.cover_url"></u-image></view>
				<view class="text">{{ item.title }}</view>
				<view class="pice">
					<view class="pice_right">￥{{ item.price }}</view>
					<view class="pice_left">销量{{ item.sales }}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			// 轮播图数据
			swiperData: [],
			// 分类数据
			categoriesData: [
				{
					cate_name: '默认'
				},
				{
					cate_name: '销量'
				},
				{
					cate_name: '推荐'
				},
				{
					cate_name: '最新'
				}
			],
			// 各分类的选中的索引
			current: 0,
			// 商品数据
			goodsData: [],
		}
	},
	onLoad() {
		this.$u.api.getIndexData().then(res => {
			this.swiperData = res.slides
			this.goodsData = res.goods
			console.log(res)
		})
	},
	methods: {
		change(index) {
			this.current = index
		}
	}
}
</script>

<style lang="scss" scoped>
.index {
	.tabs {
		margin-top: 10rpx;
	}
	.goods {
		margin-top: 10rpx;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		.goodsList {
			width: 45%;
			box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
			margin-bottom: 20rpx;
			transition: all 1s; 
			.text {
				text-align: center;
				font-size: 30rpx;
				margin-top: 5rpx;
				font-weight: 600;
			}
			.pice {
				display: flex;
				text-align: center;
				justify-content: space-between;
				margin-top: 10rpx;
				margin-bottom: 30rpx;
				.pice_right {
					width: 40%;
					color: #d4237a;
				}
				.pice_left {
					width: 40%;
					color: #c0c4cc;
					font-size: 15rpx;
				}
			}
		}
		.goodsList:hover {
			box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.5);
		}
	}
}
</style>
